.paginator {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10px;
  padding-bottom: 20px;

  -webkit-animation: 1.5s ease 0s normal forwards 1 fadein;
  animation: 1.5s ease 0s normal forwards 1 fadein;

  &__extra {
    display: flex;
    gap: 5px;

    & button:nth-child(2) {
      margin-right: 15px;
    }
  }

  &__button {
    background: none;
    width: 65px;
    height: 40px;
    border: 1px solid rgb(190, 190, 190);
    cursor: pointer;
    font-size: 14px;

    &:hover {
      background-color: rgba(236, 236, 236, 0.644);
    }

    &:active {
      // background-color: rgb(169, 169, 169);
      background-color: rgb(226, 226, 226);

    }

    &:disabled {
      pointer-events: none;
    }

    &--left {
      border-top-left-radius: 20px;
    }

    &--right {
      border-bottom-right-radius: 20px;
    }

    &--step {
      color: rgba(255, 0, 0, 0.5);
      border: 1px solid rgba(255, 0, 0, 0.322);
      // background-color: rgba(219, 255, 219, 0.685);

      &:hover {
        background-color: rgba(255, 228, 228, 0.685);

      }
    }
  }
}


@keyframes fadein {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@-webkit-keyframes fadein {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
