$transitionValue: 0.6s;

.movie-card {
  position: relative;
  display: flex;
  overflow: hidden;
  cursor: pointer;
  border-radius: 10px;
  box-shadow: 0 0 25px rgb(24, 24, 24);
  transition: $transitionValue;
  border: 1px solid rgb(255, 255, 255);

  &:hover {
    transform: scale(1.1);
  }

  &__ranking {
    position: absolute;
    font-size: 12px;
    color: white;
    background-color: rgba(0, 0, 0, 0.7);
    border: 1px solid rgb(255, 255, 255);
    padding: 5px 10px;
    border-radius: 3px;
    border-top-right-radius: 10px;
    right: 2px;
    top: 2px;
  }

  &__img {
    width: 100%;
  }

  &--is-disabled {
    pointer-events: none;
    cursor: not-allowed;
    opacity: .6;
  }
}
