.cart {
  position: absolute;
  display: flex;
  justify-content: end;
  width: 100%;
  z-index: 2;
  background-color: rgba(0, 0, 0, 0.536);
  overflow: hidden;
  color: #fff;

  &__close-cross {
    position: absolute;
    cursor: pointer;
    left: 10px;
    top: 10px;
  }

  &__content {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 800px;
    height: 100vh;
    padding: 20px 40px;
    margin-right: -800px;

    background-color: rgba(210, 210, 210, 0.701);

    transition: 0.6s;

    &--is-open {
      margin-right: 0;
    }
  }

  &__title {
    text-align: center;
    font-size: 30px;
    height: 60px;
  }

  &__movies {
    display: grid;
    grid-template-columns: repeat(auto-fill, 200px);
    grid-auto-rows: 300px;
    justify-content: center;
    grid-gap: 30px;
    padding: 30px 0;
    border-top: 3px solid rgb(255, 255, 255);
    border-bottom: 3px solid rgb(255, 255, 255);
    flex-grow: 1;
    overflow-y: scroll;
  }

  &__movie {
    position: relative;

    &--remove {
      cursor: pointer;
      z-index: 1;
      border: 2px solid rgb(255, 0, 0);
      border-bottom-right-radius: 7px;
      color: red;
      background-color: #fff;
      padding: 0 7px;

      position: absolute;
      bottom: 10px;
      right: 5px;
    }
  }

  &__client-field {
    height: 200px;
  }
}
