.carousel {
  position: relative;
  display: flex;
  gap: 20px;

  &__button {
    position: absolute;
    font-size: 20px;
    z-index: 1;
    padding: 10px;
    cursor: pointer;
    align-self: center;
    background: none;
    border: hidden;
    color: rgb(0, 0, 0);

    &--prev {
      left: -30px;
    }
    &--next {
      right: -30px;
    }

    &:disabled {
      color: grey;
    }
  }

  &__box-items {
    height: 100%;
    overflow: hidden;
  }

  &__items {
    display: flex;
    gap: 5px;
    transition: 0.7s;
  }

  &__item {
    position: relative;
    display: flex;
    cursor: pointer;

    & h3 {
      position: absolute;
      font-size: 16px;
      text-overflow: ellipsis;
      text-align: center;
      bottom: 10px;
      left: 10px;
      color: white;
      text-shadow: 2px 2px 5px #000000;
    }

    & img {
      height: auto;
      width: 140px;
      border-radius: 5px;
      border: 1px solid rgb(255, 255, 255);

      &:hover {
        box-sizing: border-box;
        box-shadow: 0 0 10px black;
      }
    }
  }
}
